<script>
export default {
  data() {
    return {
      resourcesListShow: false
    };
  },
  methods: {
    navScroll() {
      let navigationItems = document.querySelectorAll(".nav-link");
      const makeArray = Array.from(navigationItems);
      const navChange = document.getElementById("about").getBoundingClientRect()
        .top;
      // const resourcesList = document.getElementById('resourcesList');
      // console.log(navChange);

      let navigation = document.querySelector("nav");

      if (navChange < 50) {
        // resourcesList.style.display = 'none';
        navigation.classList.add("fixed-nav");
        let navigationItems = document.querySelectorAll(".nav-link");
        const makeArray = Array.from(navigationItems);
        // console.log(makeArray);
        makeArray.forEach(navItem => {
          navItem.classList.add("fixed-nav-color");
        });
      } else {
        navigation.classList.remove("fixed-nav");
        makeArray.forEach(navItem => {
          navItem.classList.remove("fixed-nav-color");
        });
      }
    }
  },
  created() {
    window.addEventListener("scroll", this.navScroll);
  }
};
</script>

